@import "../../../../App.scss";
@import "../../../../mixin.scss";

.petInfoCard {
  width: 100%;
  padding: 9px 0;
  &.active {
    background-color: $hover-color !important;
  }
  > .row1,
  .row2,
  .row3 {
    @include flex_box();
    padding: 4px 9px;
  }

  .row1,
  .row2 {
    &__col1 {
      width: 3rem;
    }
    &__col2 {
      flex: 1;
      word-break: break-all;
    }
  } // row2
  .row3 {
    &__col1,
    &__col2 {
      flex: 1;
    }
  } // row3
}
