@import "../../../App.scss";
@import "../../../setting";
@import "../../../mixin";

body.sp {
  .App {
    display: flex;
    flex-direction: column;
    overflow-y: hidden;
    > header {
    }

    .base_page_sp {
      height: 100%;
      @include flex_box();
      @include flex_direction_column();
      &_body {
        flex: 1;
        width: 100%;
        overflow: hidden;
        @include flex_box();
        @include flex_direction_column();

        /* ================  <->  () ================ */
        .map_list_header {
          @include flex_box();
          padding: 0.5rem 0;
          position: relative;

          &#customer_sp_header {
          }

          &#estimate_sp_header {
            padding: 0.5rem;
            span {
              padding: 1em;
              width: 100%;
              background-color: $header_background_gray;
              font-weight: bold;
            }

            &::after {
              height: 0;
            }
          }

          &.file_sp__header,
          &.support_history_sp__header {
            padding: 0.5rem !important;

            &::after {
              height: 0 !important;
            }

            span {
              width: 100%;
              padding: 0.5rem;
              background-color: $base_color;
              color: #fff;
              font-weight: bold;
              font-size: 1rem !important;
              line-height: 1.5em !important;
            }
          }

          &::after {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            left: 0;
            height: 1px;
            width: 100%;
            background-color: $border_secondary;
            transform: scale(0.9);
          }

          .bottom_border_button {
            flex: 1;
            margin-left: 5px;
            &:last-of-type {
              margin-right: 5px;
            }
          }
        }

        .map_list_body {
          flex: 1;
          overflow-y: scroll;
          position: relative;
          @include scroll_display_none();
        }

        /* ================  <->  ================ */

        /* ================  ================ */
        .detail_wrap {
          @include flex_box();
          @include flex_direction_column();
          height: 100%;
          font-size: 1rem;

          .detail_header {
            padding: 0.5rem 0.5rem 0;
            background-color: #fff;
            &_inner {
              display: flex !important;
              // @include flex_space_between();
              @include flex_align_center();
              display: block;
              height: 3em;
              padding: 0 0.5em;
              background-color: $header_background_gray;
              font-weight: bold;

              &__back_btn {
                margin-right: 0.5rem;
                color: $btn_dark_background_color;
                font-size: 1.5rem;
              }

              &__label {
                @include ellipsis();
                flex: 1;
              }
            }

            &_buttons {
              @include flex_box();
              margin-top: 0.25rem;
              .btn {
                flex: 1;
                font-size: 1rem;
                &:not(:first-of-type) {
                  margin-left: 0.5rem;
                }
              }
            }
          }

          .detail_body {
            flex: 1;
            padding: 0.25rem;
            overflow: hidden;
            position: relative;

            .list_base {
              border-top: none;
              width: 100%;
            } // list_base

            > div {
              height: 100%;
              @include flex_box();
              @include flex_direction_column();

              > .ui {
                &.menu {
                  @include scroll_display_none();
                  overflow-x: scroll;
                  margin-bottom: 0.25rem;
                  > a {
                    min-width: calc(100% / 5 - 0.5em);
                  }
                }
                &.attached:not(.message) + .ui.attached.segment:not(.top) {
                  padding-top: 0 !important;
                  padding-bottom: 0 !important;
                }
              }
            }
          }

          /*  */
          .maintenance_detail_sp__body,
          .file_detail_sp__body,
          .support_history_detail_sp__body {
            padding: 0.5rem;
            &__table {
              @include scroll_display_none();
              flex: 1;
              overflow-y: scroll;

              .col_2 {
                @include flex_box();
                > div {
                  &:first-of-type {
                    flex: 1;
                  }
                  &:last-of-type {
                    width: $detail_page_col2_width;
                  }
                }

                + .col_2 {
                  margin-top: 0.5rem;
                }
              }
            }
          }
        }
        /* ================  ================ */

        .page_body_footer {
          @include flex_box();
          padding: 0.5rem;
          background-color: #ffffff;
          .btn {
            width: 100%;
            + .btn {
              margin-left: 10px;
            }
          }
        }
      }

      .btn {
        &.search-address {
          border: 1px solid $base-color;
          position: absolute;
          top: 10px;
          z-index: 10;
        }
        &.search-address {
          left: 10px;
        }
      }

      &_footer {
        background-color: $footer_color;
        color: #fff;
        text-align: center;
        font-size: 0.5rem;
      }
    } // base_page_sp_body
  }

  input,
  textarea {
    font-size: 16px !important;
    appearance: none;
    resize: none;
  }

  /*  */
  .list_base {
    width: 90%;
    height: 100%;
    margin: 0 auto;

    &#detail_estimate_list {
      width: 100%;
    }
    
    &_card_wrap {
      border-bottom: 1px solid $border_secondary;
    }
  } // list_base

  /*  */
  .card_base {
    $thumb_height: 3.7rem;
    width: 100%;
    background-color: #ffffff;
    padding: 0.75rem 0.5rem;
    font-size: 0.85rem;
    color: $text_color;

    /*  */
    &_row {
      @include flex_box();
      word-break: break-all;
    }

    /*  */
    &_col {
      @include flex_box();
      @include flex_direction_column();
    }

    /* () */
    &.is_select {
      background-color: $selectecd_color;
    }

    .row1 {
      &_col1 {
        flex: 1;
      }
      &_col2 {
        /*  */
        @include flex_box();
        @include flex_direction_column();
        @include flex_space_between();
        width: 6em;
        height: $thumb_height;
      }
      &_col3 {
        /*  */
        margin-left: 0.25rem;
      }
    } // row1

    .important {
      font-size: 1rem;
      color: $base-color;
      font-weight: bold;
    }
    .emphasis {
      color: $base-color;
    }

    .thumb {
      width: calc(#{$thumb_height} / 3 * 4);
      height: $thumb_height;
      text-align: center;
      line-height: $thumb_height;
      /* TODO  */
      background-color: #cccccc;
      vertical-align: bottom;

      &.google {
        width: $thumb_height;
      }

      img {
        height: 100%;
        width: 100%;
        box-shadow: 0 0 2px 0 rgba($color: #000000, $alpha: 0.4);
        &.no_shadow {
          box-shadow: none;
        }
      }
    }

    /*  */
    .tel_no {
      min-width: 10em;
    }

    /*  */
    .sales_contact {
      color: $emphasis_color_blue;
    }
  } // card_base
}

.searchMarkerWindow {
  &.sp {
    line-height: 1.4em;
    font-size: 0.75rem;
    width: 96% !important;
    .google {
      img {
        width: 70px;
        height: 70px;
        margin-right: 10px;
      }
    }

    .text_box {
      .btn_box {
        text-align: left;
        button {
          display: inline-block !important;
          margin: 5px 0 10px;
        }
      }

      .address {
        font-size: 1.2em;
        margin-bottom: 5px;
      }
      .to_google {
        font-size: 1.2em;
        text-align: left;
      }
    }
  }
}

/*google map*/
body.sp {
  .gmnoprint.gm-style-mtc {
    button {
      width: 100%;
    }
  }
  .googlg_map_search {
    .ui.input {
      width: 100%;
    }
  }
}