    @include border-default($color: #d24444);
  }
  &.project_rank_1 {
    //
    background-color: #0099cc !important;
    @include border-default($color: #0099cc);
  }
  &.project_rank_1 {
    //
    background-color: #0099cc !important;
    @include border-default($color: #0099cc);
  }
  &.project_rank_1 {
    //
    background-color: #0099cc !important;
    @include border-default($color: #0099cc);
  }
}

/*  */
.thumb {
  $thumb_height: 3.7rem;
  width: calc(#{$thumb_height} / 3 * 4);
  height: $thumb_height;
  text-align: center;
  line-height: $thumb_height;
  /* TODO  */
  background-color: #cccccc;
  vertical-align: bottom;

  &.google {
    width: $thumb_height;
  }

  img {
    height: 100%;
    width: 100%;
    box-shadow: 0 0 2px 0 rgba($color: #000000, $alpha: 0.4);
    &.no_shadow {
      box-shadow: none;
    }
  }
}

.rect_label {
  padding: 0;
}
