@import "../../../../../App.scss";
@import "../../../../../mixin.scss";

.project_info_tab_contents_sp {
  .btn {
    position: fixed;
    right: 6px;
  }
  > .row_wrap {
    &.row1 {
      > img {
        width: 100%;
        max-height: 200px;
      }
    }
    &.row2,
    &.row3,
    &.row5,
    &.row6,
    &.row8,
    &.row9 {
      .col2 {
        flex: 1;
        word-break: break-all;
      }
    }
    &.row4,
    &.row7 {
      .col2 {
        flex: 1;
        > div {
          word-break: break-all;
        }
      }
    }
  }
}
