@import "../../../../App.scss";
@import "../../../../mixin.scss";

.estimate_card_pc {
}

.estimate_card_sp {
  .row1,
  .row2,
  .row3,
  .row4,
  .row5,
  .row6 {
    // padding: 4px 6px;
    &__col1 {
      width: 5rem;
    }
    &__col2 {
      text-align: center;
      width: 1rem;
    }
    &__col3 {
      flex: 1;
      word-break: break-all;
    }
  }

  .row3 {
    line-height: 1.5rem;
    &__col3 {
      color: $emphasis_color_blue;
      font-size: 1rem;
    }
  }

  .row6 {
    line-height: 2rem;
    &__col3 {
      font-size: 1.5rem !important;
      margin-top: -0.05em;
    }
  }
}
