@import "../../../../App.scss";
@import "../../../../mixin.scss";

.file_card_pc {
}

.file_card_sp {
  .row1 {
    &_col1 {
      div {
        &:nth-of-type(3),
        &:nth-of-type(4) {
          @include l_solid_r_flex(4.2em);
        }
      }
    }
    &_col2 {
      height: auto !important;
      justify-content: start !important;
      .thumb {
        margin-bottom: 0.25rem;
        width: 100% !important;
      }
      .btn {
        width: 100%;
      }
    }
  } // row1

  .row2 {
    margin-top: 0.25rem;
    &_col1 {
      min-width: 11em;
    }
    &_col2 {
      @include l_solid_r_flex(4.2em);
    }
  }
}
