@import "../../../mixin.scss";

.detail_info_table {
  overflow-x: hidden;
  &_row {
    @include flex_box();
    margin-top: 0.5em;
    .header {
      padding: 0.5em;
      width: 40vw;
      max-width: 9em;
      background-color: $table_thead_background_color;
    }
    .data {
      flex: 1;
      word-break: break-all;
      padding: 0 0.5rem;
      &:not(.address) {
        @include flex_box();
        @include flex_align_center();
      }

      &.email {
        padding: 0 0 0 0.5rem;
        div + div {
          margin-left: 0.5rem;
        }
      }
    }
  }
}
